









import Vue from 'vue';
import Component from 'vue-class-component';
import DCascader from '../../index';

Vue.use(DCascader);
@Component({
  name: 'Demo'
})
export default class Demo extends Vue {

  public value = [null, null, null];
  public options = [{
    value: 'zhejiang',
    label: '浙江',
    isLeaf: false,
    loading: false,
    children: [{
      label: '杭州', value: 'hz'
    }, {
      label: '温州', value: 'wz'
    }]
  }, {
    value: 'jiangsu',
    label: '江苏',
    isLeaf: false,
    loading: false,
    children: [{
      label: '南京', value: 'nj',
      children: [{
        label: '鼓楼区', value: 'gl'
      }, {
        label: '玄武区', value: 'xw'
      }]
    }, {
      label: '苏州', value: 'sz'
    }]
  }];

  public mounted() {
  }

  public async loadOptions(selectedOptions: any) {
    const targetOption = selectedOptions[selectedOptions.length - 1];
    targetOption.loading = true;
    setTimeout(() => {
      targetOption.loading = false;
      targetOption.children = [{
        label: '南京', value: 'nj'
      }, {
        label: '广州', value: 'gz'
      }];
    }, 1000);
  }

  public onChange(value) {
    console.log(value);
  }
}
